  0% {
    -webkit-transform: scale(0.6);
    transform: scale(0.6); }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
    border-width: 12px; }
  100% {
    -webkit-transform: scale(0.6);
    transform: scale(0.6); } }

